<template>
  <div>
    <Loadings v-if="list == null"></Loadings>
    <div class="container scontainer" v-else>
      <div class="search-bar flex-sb">
        <van-dropdown-menu active-color="#515151">
          <van-dropdown-item
            @change="selectOption"
            v-model="svalue"
            :options="soption"
          />
        </van-dropdown-menu>
        <div class="search">
          <van-search
            v-model="keywords"
            show-action
            label="查询"
            placeholder="请输入搜索关键词"
            @search="searchData"
            clearable
          >
            <template #action>
              <div @click="searchData">搜索</div>
            </template>
          </van-search>
        </div>
      </div>
      <div class="setting">
        <van-icon
          @click="openSetting"
          color="gray"
          size="20"
          name="setting-o"
        />
      </div>
      <section class="query">
        <Empty v-if="list.length == 0"></Empty>
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
          class="list"
          v-else
        >
          <router-link
            :to="{ path: '/InvoiceDetails/' + item.oid }"
            class="item"
            v-for="(item, index) in list"
            :key="index"
          >
            <div class="flex-sb">
              <div class="info">
                <div class="flex-sb h2">
                  <div>订单号: {{ item.order_no }}</div>
                  <div
                    style="
                      text-align: right;
                      font-size: 12px;
                      color: #333;
                      position: relative;

                      width: 30%;
                    "
                  >
                    {{ item.status == 0 ? "未完成" : "已完成" }}
                  </div>
                </div>
                <div class="flex-sa">
                  <div>客户名称: {{ item.user_name }}</div>
                  <div>数量: {{ item.number }}</div>
                </div>
                <div class="flex-sa">
                  <div>材质名称: {{ item.name }}</div>
                  <div>材质: {{ item.material }}</div>
                </div>
                <div class="flex-sa">
                  <div>尺寸长: {{ item.length }}</div>
                  <div>尺寸宽: {{ item.width }}</div>
                </div>

                <div class="flex-sa">
                  <div>创建日期: {{ formatDate(item.add_time) }}</div>
                  <div>交货日期: {{ formatDate(item.com_time) }}</div> 
                </div>
                <div>备注: {{ item.remark }}</div>
              </div>
              <van-icon name="arrow" color="#ccc" />
            </div>
          </router-link>
        </van-list>
      </section>

      <!--setting-->
    </div>
    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" />
  </div>
</template>

<script>
import { getPorder, getPorderDetail } from "@api/public";
import {
  Icon,
  Search,
  ActionSheet,
  Cell,
  CellGroup,
  List,
  DropdownMenu,
  DropdownItem,
} from "vant";
import Empty from "@components/Empty";
import localStorage from "@utils/store/localStorage";
import Loadings from "@components/Loadings";
export default {
  name: "DataCenter",
  components: {
    [Search.name]: Search,
    [DropdownMenu.name]: DropdownMenu,
    [DropdownItem.name]: DropdownItem,
    [List.name]: List,
    [Icon.name]: Icon,
    Loadings,
    Empty,
    [ActionSheet.name]: ActionSheet,
  },
  props: {},
  data: function () {
    return {
      list: null,
      show: false,
      keywords: "",
      month: ["2020", "0"],
      page: 1,
      loading: false,
      finished: false,
      svalue: 0,
      soption: [
        { text: "月份", value: 0 },
        { text: "1月", value: 1 },
        { text: "2月", value: 2 },
        { text: "3月", value: 3 },
        { text: "4月", value: 4 },
        { text: "5月", value: 5 },
        { text: "6月", value: 6 },
        { text: "7月", value: 7 },
        { text: "8月", value: 8 },
        { text: "9月", value: 9 },
        { text: "10月", value: 10 },
        { text: "11月", value: 11 },
        { text: "12月", value: 12 },
      ],
      actions: [
        { name: "修改密码" },
        { name: "系统刷新" },
        { name: "退出登录" },
      ],
    };
  },
  created() {
    var date = new Date();
    var month = this.month;
    month[0] = date.getFullYear();
    this.month = month;
    this.getData();
  },
  mounted: function () {},
  methods: {
    searchData() {
      if (this.keywords != "") {
        this.page = "";
      }
      this.getData();
    },
    selectOption(e) {
      var month = this.month;
      month[1] = e;
      this.month = month;
      this.page = 1;
      this.getData();
    },
    onSelect(e) {
      if (e.name == "修改密码") {
        this.show = false;
        this.$router.replace({ path: "/change_password" });
      } else if (e.name == "系统刷新") {
        this.show = false;
        localStorage.remove("ntoken");
        this.$dialog.message("清除成功");
      } else {
        this.show = false;
        localStorage.remove("ntoken");
        this.$router.replace({ path: "/" });
      }
    },
    openSetting() {
      this.show = true;
    },
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
    getData() {
      var data = {
        search: this.keywords,
        page: this.page,
      };
      if (this.month[1] != 0) {
        data.month = this.month;
      }

      getPorder(data).then((res) => {
        this.list = res.data.list;
        if (this.page > 1) {
          this.list = this.list.concat(res.data.list);
        } else {
          this.list = res.data.list;
        }
        this.finished = res.data.list.length < 20 ? true : false;
        this.loading = res.data.list.length < 20 ? true : false;
        if (res.data.list.length >= 20) {
          this.page = this.page + 1;
        }
      });
    },
    onLoad() {
      this.getData();
    },
  },
};
</script>
<style scoped>
.sales-data {
  background: #092e48;
  padding: 20px 0;
}
.van-dropdown-menu {
  width: 70px;
}
.search {
  width: calc(100% - 70px);
}
.sales-data .caption {
  color: #fff;

  padding-left: 19px;
  margin-bottom: 10px;
  font-size: 16px;
}
.setting {
  position: fixed;
  top: 17px;
  left: 10px;
  z-index: 6666666666666666;
  color: red;
  display: none;
}

.sales-data .flex-sa {
  height: 110px;
  border-radius: 5px;
  background: #fff;
  margin: 0 9px;

  box-shadow: 0 0 5px #ccc;
  text-align: center;
}
.container {
  padding-bottom: 60px;
}
.sales-data .flex-sa .title {
  color: #999;
  font-size: 12px;
  margin-top: 5px;
}

.to-do,
.query {
  box-shadow: 0 0 5px #eee;
  background-color: #fff;
  margin: 7px;
  border-radius: 5px;
}

.to-do .caption,
.query .caption {
  font-weight: 600;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #666;
}

.to-do .list {
  text-align: center;
  padding: 15px 0;
}

.to-do .list .div {
  display: inline-block;
  padding: 0 5px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  margin: 0 3px;
  min-width: 27%;
  color: #666;
  background-color: #f9f9f9;
  font-size: 12px;
  position: relative;
}
.to-do .list .div .fixed {
  position: absolute;
  top: -8px;
  right: -5px;
  line-height: 17px;
  min-width: 20px;
  padding: 0 2px;
  height: 15px;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 0 8px #ccc;
  background: #ed4e4e;
  color: #fff;
  font-size: 10px;
}

.to-do .list .div img {
  width: 20px;
  vertical-align: sub;
}

.query {
  padding-bottom: 20px;
}

.query .list {
  margin-bottom: 5px;
}

.query .list .item {
  text-align: center;
  font-size: 12px;
  padding: 10px;
  display: block;
  text-align: left;
  color: #666;
  line-height: 20px;
   
  border-bottom: 10px solid #f9f9f9; 
}
.query .list .item .h2 {
  font-size: 14px;
  color: #d2ac5b;
}
.query .list .item .info {
  width: calc(100% - 30px);
}
.query .list .item .info div {
  width: 100%;
}

.value {
  color: #000;
  font-size: 22px;
}

.value span {
  font-size: 12px;
  color: #999;
}

.header-top {
  text-align: center;
  background: #131418;
  color: #fff;
  padding: 10px 0;
}

.header-bottom {
  overflow: hidden;
}

.header-bottom div {
  float: left;
  color: #8c8d91;
  font-size: 14px;
  width: 50%;
  text-align: center;
}

.header-bottom div:first-child {
  border-right: 1px dashed #8c8d91;
}

.header-bottom div span {
  color: #fff;
}

.header-top .center {
  padding: 35px 0;
}

.header-top .center div {
  font-size: 36px;
  font-weight: bold;
  color: #d2ac5b;
}

.header-top .center p {
  color: #8c8d91;
  margin-bottom: 10px;
}

.header-top .caption-name {
  color: #8c8d91;
  font-size: 18px;
}
</style>
